import React from 'react';
import s from './Projects.module.css'
import styles from '../common/styles/Container.module.scss'
import Project from "./Project/Project";
import Title from "../common/components/Title";
import SubTitle from "../common/components/SubTitle";
import {Fade} from "react-awesome-reveal";


const Projects = () => {

    let projects = [
        {
            title: "Social Network",
            icon: "https://i.pinimg.com/564x/11/84/57/11845746c07479ddb280cb7ad57050e5.jpg",
            description: "use old syntax's React, HOC, Redux,Type Script, work in REST API, thunk, Unit Tests.",
            link: "https://artemefimovich.github.io/social-network/#/profile",
        },
        {
            title: "Todo list",
            icon: "https://encrypted-tbn0.gstatic.com/images?q=tbn:ANd9GcQO2AqMx1yrjS9cI_Cv82K91hsbNhJUFPSPog&usqp=CAU",
            description: "React,Type Script, Redux, RestAPI, Thunk, Material UI, Unit Tests, StoryBook",
            link: "https://artemefimovich.github.io/todolist/#/",
        },
        {
            title: "Cards",
            icon: "http://www.bravercardgame.com/img/icon-qucards-crave.png",
            description: "React,Type Script, Redux, RestAPI, Thunk, Material UI, Redux Tool Kit",
            link: "https://pointjkee.github.io/Final-Project-Friday/#/login",
        },
        {
            title: "Films",
            icon: "data:image/jpeg;base64,",
            description: "React,Type Script, RestAPI, Material UI",
            link: "https://artemefimovich.github.io/movies_app/#/",
        },
    ];


    return (
        <div className={s.projectsBlock} id={'projects'}>
            <Fade>
                <div className={`${styles.container} ${s.projectsContainer}`}>
                    <SubTitle subTitle="Check my projects and keep your feedback"/>
                    <Title title="Projects"/>
                    <div className={s.projects}>
                        {projects.map(({title, icon, description, link}) => {
                            return <Project key={title}
                                title={title}
                                icon={icon}
                                description={description}
                                link={link}/>
                        })}
                    </div>
                </div>
            </Fade>
        </div>
    );
};

export default Projects;




